<script>
  import {exportTableData} from '../../assets/js/tableExport'
  export default {
    name: "examAnalysis",
    data() {
      return {
        dateRange: "", //时间选择
        sectionName: "", //断面名称
        value: "",
        optionData: [
          { value: 1, label: "水温", standard: "", isShow: true },
          { value: 2, label: "PH", standard: "6~9", isShow: true },
          { value: 3, label: "溶解氧", standard: "≥5", isShow: true },
          { value: 4, label: "电导率", standard: "", isShow: true },
          { value: 5, label: "浊度", standard: "", isShow: true },
          { value: 6, label: "高锰酸盐", standard: "6", isShow: true },
          { value: 7, label: "氨氮", standard: "1", isShow: true },
          { value: 8, label: "总磷", standard: "0.2", isShow: true },
          { value: 9, label: "总氮", standard: "", isShow: true }
        ],
        stationId:'',
        tableData: [],
        dayQualityData:[],//日质控绑定数据
        weekQualityData:[],//周质控绑定数据
        linearityData:[],//月质控-多点线性绑定数据
        interventionData:[],//月质控-集成干预
        comparisonData:[],//月质控-实际水样对比
        recoveryData:[],//月质控-加标回收
        projectSet: false,
        dialogData: [
          {
            monitoringItems: "高锰酸钾",
            waterQuality: "",
            spanValue: "",
            zeroPoint: "",
            span: ""
          },
          {
            monitoringItems: "氨氮",
            waterQuality: "",
            spanValue: "",
            zeroPoint: "",
            span: ""
          },
          {
            monitoringItems: "总磷",
            waterQuality: "",
            spanValue: "",
            zeroPoint: "",
            span: ""
          },
          {
            monitoringItems: "总氮",
            waterQuality: "",
            spanValue: "",
            zeroPoint: "",
            span: ""
          }
        ],
        tabsValue:'DAY_QUALITY',
      };
    },
    methods: {
      toggleTab(val) {
         this.getQualityControllerData();
      },
       //查询数据
      getQualityControllerData(){
        this.$http.get("/Quality/qualityAnalysis/monthReportStandingBook", {
            params: {
              startTime: this.dateRange[0].format('yyyy-MM-dd'),
              endTime: this.dateRange[1].format('yyyy-MM-dd'),
              stationCodes: this.stationId, //this.stationId
              qualityAnalysisType: this.tabsValue,
            }
          }).then(res => {
            console.log(res);
            if (res.data.code === 0) {
              switch (this.tabsValue) {
                case 'DAY_QUALITY':
                  this.dayQualityData = res.data.content.dataList;
                  break;
                case 'WEEK_QUALITY':
                  this.weekQualityData = res.data.content.dataList;
                  break;
                case 'MULTIPOINT_LINEARITY':
                  this.linearityData = res.data.content.dataList;
                  break;
                case 'INTEGRATED_INTERVENTION':
                  this.interventionData = res.data.content.dataList;
                  break;
                case 'REAL_WATER_SAMPLE_COMPARISON':
                  this.comparisonData = res.data.content.dataList;
                  break;
                case 'RECOVERY':
                  this.recoveryData = res.data.content.dataList;
                  break;
              }
            }
          }).catch(error => {});
      },
      //导出表格
      exportExcel(){
        let {tableA,tableB,tableC,tableD,tableE,tableF} = this.$refs;
        let table = null;
        let title = '';
        let headList = [];
        switch (this.tabsValue) {
          case 'DAY_QUALITY':
            table = tableA;
            title = '日质控';
            headList = ['areaName','basinName','stationName','operationName','w01019_qualifiedRate','w21003_qualifiedRate','w21011_qualifiedRate','w21001_qualifiedRate'
                        ,'appraiseQualifiedRate','w01019_completionRate','w21003_completionRate','w21011_completionRate','w21001_completionRate','appraiseCompletionRate'];
            break;
          case 'WEEK_QUALITY':
            table = tableB;
            title = '周质控';
            headList = ['areaName','basinName','stationName','operationName','w01001_qualifiedRate','w01009_qualifiedRate','w01014_qualifiedRate'
                        ,'w01003_qualifiedRate','appraiseQualifiedRate','w01001_completionRate','w01009_completionRate','w01014_completionRate'
                        ,'w01003_completionRate','appraiseCompletionRate'];
                         
            break;
          case 'MULTIPOINT_LINEARITY':
            table = tableC;
            title = '月质控-多点线性';
            headList = ['areaName','basinName','stationName','operationName','w01019_qualifiedRate','w21003_qualifiedRate','w21011__qualifiedRate','w21001__qualifiedRate'
                        ,'w01019_completionRate','w21003_completionRate','w21011__completionRate','w21001__completionRate'];
            break;
          case 'INTEGRATED_INTERVENTION':
            table = tableD;
            title = '月质控-集成干预';
            headList = ['areaName','basinName','stationName','operationName','w01019_qualifiedRate','w21003_qualifiedRate','w21011__qualifiedRate','w21001__qualifiedRate'
                        ,'w01019_completionRate','w21003_completionRate','w21011__completionRate','w21001__completionRate'];
            break;
          case 'REAL_WATER_SAMPLE_COMPARISON':
            table = tableE;
            title = '月质控-实际水样对比';
            headList = ['areaName','basinName','stationName','operationName','w01010_qualifiedRate','w01001_qualifiedRate','w01009_qualifiedRate'
                        ,'w01014_qualifiedRate','w01003_qualifiedRate','w01010_completionRate','w01001_completionRate','w01009_completionRate'
                        ,'w01014_completionRate','w01003_completionRate']
            break;
          case 'RECOVERY':
            table = tableF;
            title = '月质控-加标回收';
            headList = ['areaName','basinName','stationName','operationName','w01019_qualifiedRate','w21003_qualifiedRate','w21011_qualifiedRate'
                        ,'w21001_qualifiedRate','w01019_completionRate','w21003_completionRate','w21011_completionRate','w21001_completionRate'];
            break;
        }
        let tablewrap = table.$el;
        // let tableBox = tablewrap.querySelector('.el-table__fixed');//el-table__fixed
        let header = tablewrap.querySelector('.el-table__header');
        
        let params = {
          startTime: this.dateRange[0].format('yyyy-MM-dd'),
          endTime: this.dateRange[1].format('yyyy-MM-dd'),
          stationCodes: this.stationId, //this.stationId
          qualityAnalysisType: this.tabsValue,
          headerProp:headList.join(','),
          tableType:'MONTH_REPORT',
          pattern:'yyyy-MM-dd'
        };
        exportTableData(header,title,'','1',"/Quality/exports/qualityControlExcel",params);
      },
    },
    computed: {
      stationInfo: {
        get: function() {
          return this.$store.state.stationInfo;
        }
      }
    },
    created() {
      this.dateRange = [new Date(new Date().setMonth(new Date().getMonth()-1)),new Date()];
      this.$store.commit("setShowlefttree", true);
    },
    computed: {
      stationInfo: {
        get: function() {
          return this.$store.state.stationInfo;
        }
      }
    },
    watch: {
      stationInfo(value) {
        if (value.length > 0) {
          let list = [];
          value.forEach((item)=>{
            list.push(item.nodeId);
          });
          this.stationId = list.join(',');
          this.getQualityControllerData();
        }
      }
    }
  };
</script>

<style scoped lang="less">
  .examAnalysis {
    height: calc(100% - 30px);
    .containerbox {
      padding: 6px 12px;
      height: calc(100% - 6px);
    }
  }
  .editInput {
    width: 50px;
    border: none;
    outline: none;
  }
  .toolbox-row {
    margin-bottom: 6px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>



